@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body{
    font-family: "Poppins";
    font-weight: 400;
    background-color: hsl(0, 100%, 74%);
    background-image: url(/reactjsassignment/static/media/bg-intro-mobile.02ce3982.png);        
}

.container{
    /* display:grid ;
    margin-top: 300px;
    max-width: 1000px;
    margin-left: 700px; */
    /* justify-content: center; */
    padding:40px;
    max-width: 1536px;
    margin: auto;
}

.text{
    /* width: 70%;
    grid-column: 2/3;
    grid-row: 2/3; */
    /* grid-area: main; */
    margin: 10px;
    color: white;
}

.text h1{
    font-size: 24px;
}

.try{
    /* width: 110%; */
    /* height: fit-content; */
    background-color:hsl(249, 43%, 39%) ;
    color: white;
    border-radius: 15px;
    text-align: center;
    /* grid-column: 3; */
    margin: 8px auto;
    padding: 10px;    
}
.info{
    
    text-align: center;
    /* width: 100%; */
    /* grid-area: info; */
    background-color: white;
    border-radius: 15px;
    padding: 20px;
    /* grid-column: 3; */
}

.form-input {
    width: 100%;
    display:inline-block;
    padding: 14px ;
    margin: 8px auto;
    border-radius: 10px;
    border: solid hsl(246, 25%, 77%);
    /* border-width: .5px; */   
}

input[type=submit] {
    /* width: 84%; */
    background-color: #4CAF50;
    color: white;
    padding: 14px;
    margin: 8px auto;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }



footer p{
    font-size: 10px;
    color: hsl(246, 25%, 77%); ;   
}

footer a{
    color: orange;
}


@media only screen and (min-width: 600px) {
    .container{
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: center;
                align-items: center;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        padding: 60px;
    }

    .container > div {
        -webkit-flex: 1 1;
                flex: 1 1;
        margin: 10px;

    }

    .text h1{
        font-size: 32px;
    }
}
@media only screen and (min-width: 768px) {
    body{

        background-image: url(/reactjsassignment/static/media/bg-intro-desktop.059183ee.png);    
    }

    .container{
        padding: 100px;
    }


    
}
